 * 🦊 Nox Enterprise Chat Interface Styles
 * Complete Aurora-themed styling for VS Code sidebar chat
 */

/* Aurora Theme Variables */
:root {
  --aurora-blue: #4c9aff;
  --aurora-purple: #8b5cf6;
  --aurora-green: #10b981;
  --aurora-pink: #f472b6;
  --aurora-cyan: #06b6d4;
  --aurora-orange: #ff6b35;
  --bg-primary: var(--vscode-sideBar-background);
  --bg-secondary: var(--vscode-sideBarSectionHeader-background);
  --text-primary: var(--vscode-sideBar-foreground);
  --text-secondary: var(--vscode-descriptionForeground);
  --border-color: var(--vscode-sideBarSectionHeader-border);
  --input-bg: var(--vscode-input-background);
  --input-border: var(--vscode-input-border);
  --button-bg: var(--aurora-blue);
  --button-hover: var(--aurora-purple);
}

/* Reset and Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--vscode-font-family);
  font-size: var(--vscode-font-size);
  background: var(--bg-primary);
  color: var(--text-primary);
  height: 100vh;
  overflow: hidden;
}

/* Aurora Background */
.aurora-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    rgba(76, 154, 255, 0.1) 0%,
    rgba(139, 92, 246, 0.1) 25%,
    rgba(16, 185, 129, 0.1) 50%,
    rgba(244, 114, 182, 0.1) 75%,
    rgba(6, 182, 212, 0.1) 100%
  );
  background-size: 400% 400%;
  animation: aurora-flow 15s ease-in-out infinite;
  pointer-events: none;
  z-index: -1;
}

@keyframes aurora-flow {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

/* Main Container */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: relative;
}

/* Provider Controls Header */
.provider-controls {
  background: rgba(15, 23, 42, 0.95);
  border-bottom: 1px solid var(--aurora-blue);
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  backdrop-filter: blur(10px);
}

.provider-selector,
.model-selector {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
}

.provider-selector label,
.model-selector label {
  color: var(--aurora-blue);
  font-weight: 600;
  min-width: 60px;
}

.provider-dropdown,
.model-dropdown {
  flex: 1;
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid var(--aurora-blue);
  border-radius: 6px;
  color: var(--text-primary);
  padding: 4px 8px;
  font-size: 11px;
  outline: none;
  transition: all 0.2s ease;
}

.provider-dropdown:focus,
.model-dropdown:focus {
  border-color: var(--aurora-purple);
  box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.2);
}

.provider-status {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 10px;
}

.status-indicator {
  font-size: 8px;
  color: var(--aurora-green);
}

.status-indicator.error {
  color: #ef4444;
}

.status-indicator.warning {
  color: #f59e0b;
}

.status-text {
  color: var(--text-secondary);
}

.cost-tracker {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  color: var(--text-secondary);
  padding-top: 4px;
  border-top: 1px solid rgba(139, 92, 246, 0.2);
}

.cost-info,
.token-info {
  display: flex;
  align-items: center;
  gap: 4px;
}

.cost-value,
.token-value {
  color: var(--aurora-blue);
  font-weight: 600;
}

/* Messages Container */
.messages-container {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.messages-container::-webkit-scrollbar {
  width: 6px;
}

.messages-container::-webkit-scrollbar-track {
  background: transparent;
}

.messages-container::-webkit-scrollbar-thumb {
  background: var(--aurora-blue);
  border-radius: 3px;
}

/* Welcome Message */
.welcome-message {
  text-align: center;
  padding: 20px 10px;
  background: linear-gradient(
    135deg,
    rgba(76, 154, 255, 0.1),
    rgba(139, 92, 246, 0.1)
  );
  border-radius: 12px;
  border: 1px solid rgba(76, 154, 255, 0.2);
}

.fox-welcome {
  font-size: 24px;
  margin-bottom: 8px;
}

.welcome-title {
  color: var(--aurora-blue);
  font-weight: 600;
  margin-bottom: 4px;
}

.welcome-subtitle {
  color: var(--text-secondary);
  font-size: 11px;
}

/* Messages */
.message {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 8px;
  border-radius: 8px;
  position: relative;
}

.message.user {
  background: linear-gradient(
    135deg,
    rgba(244, 114, 182, 0.1),
    rgba(139, 92, 246, 0.05)
  );
  border-left: 3px solid var(--aurora-pink);
  margin-left: 20px;
  margin-right: 0;
}

.message.assistant {
  background: linear-gradient(
    135deg,
    rgba(16, 185, 129, 0.1),
    rgba(76, 154, 255, 0.05)
  );
  border-left: 3px solid var(--aurora-green);
  margin-left: 0;
  margin-right: 20px;
}

.message-content {
  color: var(--text-primary);
  line-height: 1.4;
  font-size: 13px;
  word-wrap: break-word;
}

.message-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  color: var(--text-secondary);
  margin-top: 4px;
  padding-top: 4px;
  border-top: 1px solid rgba(139, 92, 246, 0.1);
}

.message-info {
  display: flex;
  gap: 8px;
  align-items: center;
}

.message-cost {
  color: var(--aurora-green);
  font-weight: 600;
}

.message-tokens {
  color: var(--aurora-cyan);
}

.message-time {
  color: var(--text-secondary);
}

/* Input Area */
.input-container {
  padding: 12px;
  background: rgba(15, 23, 42, 0.95);
  border-top: 1px solid var(--aurora-blue);
  backdrop-filter: blur(10px);
}

.input-wrapper {
  display: flex;
  gap: 8px;
  align-items: flex-end;
}

.message-input {
  flex: 1;
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid var(--aurora-blue);
  border-radius: 8px;
  color: var(--text-primary);
  padding: 8px 12px;
  font-size: 13px;
  line-height: 1.4;
  resize: none;
  outline: none;
  transition: all 0.2s ease;
  min-height: 36px;
  max-height: 120px;
}

.message-input:focus {
  border-color: var(--aurora-purple);
  box-shadow: 0 0 0 2px rgba(139, 92, 246, 0.2);
}

.send-button {
  background: var(--button-bg);
  border: none;
  border-radius: 6px;
  color: white;
  padding: 8px 12px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 60px;
}

.send-button:hover {
  background: var(--button-hover);
  transform: translateY(-1px);
}

.send-button:active {
  transform: translateY(0);
}

.send-button:disabled {
  background: rgba(76, 154, 255, 0.3);
  cursor: not-allowed;
  transform: none;
}

/* Thinking Indicator */
.thinking-indicator {
  display: none;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: linear-gradient(
    135deg,
    rgba(139, 92, 246, 0.1),
    rgba(76, 154, 255, 0.05)
  );
  border-radius: 8px;
  border-left: 3px solid var(--aurora-purple);
  margin-left: 20px;
  margin-right: 0;
  font-size: 12px;
  color: var(--text-secondary);
}

.thinking-indicator.show {
  display: flex;
}

.thinking-dots {
  display: flex;
  gap: 2px;
}

.thinking-dot {
  width: 4px;
  height: 4px;
  background: var(--aurora-purple);
  border-radius: 50%;
  animation: thinking-pulse 1.4s ease-in-out infinite both;
}

.thinking-dot:nth-child(1) {
  animation-delay: -0.32s;
}
.thinking-dot:nth-child(2) {
  animation-delay: -0.16s;
}
.thinking-dot:nth-child(3) {
  animation-delay: 0;
}

@keyframes thinking-pulse {
  0%,
  80%,
  100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}
